import './MineField.scss';

import Cell from 'components/Cell';
import { MinesweeperContext } from 'contexts/minesweeper';
import React, { SyntheticEvent, useCallback, useContext } from 'react';
import { joinClassNames } from 'utils/join-class-names';

const MineField = () => {
  const {
    mineFieldParams: { rowsCount },
    mineField,
    mineFieldStatus: { isSetup, isBusted, isSolved },
    setupMineField,
    revealCell,
    toggleFlag,
    revealNeighbors,
  } = useContext(MinesweeperContext);

  const className = 'MineField';
  const rowClassName = `${className}Row`;

  const cellWidth = 32;

  const handleContextMenu = (event: SyntheticEvent<HTMLDivElement>) => {
    event.preventDefault();
  };

  const handleCellClick = useCallback(
    (cell: Cell) => {
      (isSetup ? revealCell : setupMineField)(cell);
    },
    [isSetup, revealCell, setupMineField]
  );

  const handleCellMouseDown = useCallback(
    (
      { target, nativeEvent }: SyntheticEvent<HTMLButtonElement>,
      cell: Cell
    ) => {
      const handleMouseUp = (event: Event) => {
        if ((nativeEvent as MouseEvent).which !== (event as MouseEvent).which) {
          revealNeighbors(cell);
        }

        target.removeEventListener('mouseup', handleMouseUp);
      };

      target.addEventListener('mouseup', handleMouseUp);
    },
    [revealNeighbors]
  );

  const handleCellContextMenu = useCallback(
    (cell: Cell) => {
      toggleFlag(cell);
    },
    [toggleFlag]
  );

  return (
    <div
      className={className}
      style={{ width: rowsCount * cellWidth }}
      onContextMenu={handleContextMenu}
    >
      <div
        className={joinClassNames(
          rowClassName,
          `${rowClassName}__count-${rowsCount}`,
          `${isBusted || isSolved ? `${rowClassName}__disabled` : ''}`
        )}
      >
        {mineField.map((cell) => (
          <Cell
            cell={cell}
            onClick={handleCellClick}
            onMouseDown={handleCellMouseDown}
            onContextMenu={handleCellContextMenu}
            key={cell.id}
          />
        ))}
      </div>
    </div>
  );
};

export default MineField;
