import { useEffect, useRef } from 'react';

type Fn = () => void;

export const useInterval = (fn: Fn, delay?: number) => {
  const fnRef = useRef<Fn>();

  useEffect(() => {
    fnRef.current = fn;
  });

  useEffect(() => {
    if (!delay) {
      return void 0;
    }

    const intervalId = setInterval(() => {
      fnRef.current?.();
    }, delay);

    return () => {
      clearInterval(intervalId);
    };
  }, [delay]);
};
